<template>
<div>
    <h3 >Exmenes de Hoy</h3>
                <v-simple-table 
                    fixed-header
                    class="my-2"    
                   height="500px"
                    >
                    <template v-slot:default>
                    <thead>
                    <tr>
                    <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text " 
                    style="border-top-left-radius:5px;border-bottom-left-radius:5px" >
                        Nro
                    </th>
                    <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        Paciente
                    </th>
                        <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                            Examen
                        </th>
                        <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                            
                        </th>
                        <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                            
                        </th>

                        <th class="red darken-4 text-center subtitle-1 font-weight-bold white--text " 
                        style="border-top-right-radius:5px;border-bottom-right-radius:5px">
                         
                        </th>
                    </tr>
                    </thead>
                        <tbody>
                        <tr v-for="(item,index) in dataTablaResultado" :key="index"  class="font-weight-bold red lighten-4"  >
                            <td class="text-center">{{index +1}}</td>
                            <td class="text-center">{{item.nombres}} {{item.apellidos}}</td>
                            <td class="text-center red--text text-darken-4 " >{{item.examen}}</td>
                            <td class="text-center red--text text-darken-4 " >
                               <v-btn @click="()=>{dialogComentario=true,comentario=item.descripcion}"  elevation="3" icon >
                                    <v-icon>speaker_notes</v-icon> 
                                </v-btn>
                            </td>
                            <td class="text-center red--text text-darken-4  ">                        
                               
                                  <v-btn @click="descargarPDF(item.idResultado)"  elevation="3" icon >
                                    <v-icon>get_app</v-icon> 
                                </v-btn>
                                
                            </td>
                            <td class="text-center red--text text-darken-4  ">                        
                               
                                  <v-btn @click="eliminar(item.idExamen)"  elevation="3" icon >
                                    <v-icon>minimize</v-icon> 
                                </v-btn>
                                
                            </td>
                        </tr>
                        </tbody>
                  </template>
                </v-simple-table>   
                <v-dialog v-model="dialogComentario" max-width="290" >
               
                <v-card class="py-2">
                    <v-list-item-content class="text-center">
                    <div class="font-weight-bold"> Comentario </div>
                    </v-list-item-content>                                
                    <v-list-item-content>
                       <div class="text-center">
                            {{comentario==''?'SIN MENSAJE':comentario}}
                       </div>
                    </v-list-item-content>
                   <v-list-item-content>
              <v-layout mx-2 :justify-center="true">
                <v-btn @click="()=>{dialogComentario=false, comentario=''}" dark>Cerrar</v-btn>
              </v-layout>
        </v-list-item-content>
                </v-card>

               </v-dialog>    
</div>
</template>
<script>
import JSPDF from 'jspdf'
import {mapState} from 'vuex'
export default {
name:'ListaExamenesHoy',
data() {
    return {
        comentario:'',
        dialogComentario:false,
        dataTablaResultado:[],
           }
    },
  computed:{
      ...mapState(['token'])
  },
  mounted(){
    this.listarResultados();
  },
    methods: {
      listarResultados(){
                    var headers =  {
                        "Content-Type": "application/json",
                        token: this.token,
                    };
 
                    this.axios.get('/listarResultados',{headers})
                    .then(res=>{ 
                        
                    this.dataTablaResultado=res.data;
                    // //Como laconsulta delresultado no trae el nombre del examen ,
                    // //Sacamos el nombre de las listas de los examenes de acuerdo al IdExamen            
                    //        this.dataTablaResultado.map(e=>{
                    //             recorrer:
                    //             for (let index = 0; index < dataNombreExamen.length; index++) {
                    //                 if(e.idExamen==dataNombreExamen[index].idExamen){
                    //                     e.examen=dataNombreExamen[index].examen
                    //                     break recorrer;
                    //                 }          
                    //             }
                    //             return e
                    //     })
                    })
                    .catch(err=> console.log(err))
                },
      generarPDF(){
          this.ruta='';
          const doc = new JSPDF();
          
            doc.setFillColor(0, 0, 255)
            doc.ellipse(10, 5, 10, 5, 'F');
            doc.setLineWidth(1)
            doc.setDrawColor(0)
            doc.setFillColor(255, 0, 0)
            doc.circle(120, 20, 5, 'FD')
         //doc.save('test.pdf');
          this.ruta=doc.output('datauri');

          console.log(this.ruta);
          this.ruta='/'+this.ruta.substring(this.ruta.indexOf("base64,")+7);
          console.log(this.ruta)
        //   console.log(this.$route.query.page);
        //    this.$refs.modal_pdf.click()
           window.open(doc.output('bloburl'), '_blank');

      },
         verPDF(idPDF){                    
                        this.axios.post('/getFile',{id:idPDF})
                        .then(res =>{
                            var arrayBuffer=res.data.pdf.data;       
                            var array = new Uint8Array(arrayBuffer);
                            var blob = new Blob([array], { type: 'application/pdf' });
                                const blobUrl = URL.createObjectURL(blob);
                                    window.open(blobUrl, '_blank');

                            }).catch(err =>{console.log(err)})
                    }, 
                        descargarPDF(idPDF){                    
                        this.axios.post('/getFile',{id:idPDF})
                        .then(res =>{
                                var nombreArchivo=res.data.namepdf
                                 var arrayBuffer=res.data.pdf.data;       
                                var array = new Uint8Array(arrayBuffer);
                                var blob = new Blob([array], { type: 'application/pdf' });
                                const blobUrl = URL.createObjectURL(blob);
                                    var downloadLink = document.createElement("a");
                                    downloadLink.href = blobUrl;
                                    downloadLink.download = nombreArchivo;

                                    document.body.appendChild(downloadLink);
                                    downloadLink.click();
                                    document.body.removeChild(downloadLink);
                            

                            }).catch(err =>{console.log(err)})
                    }, 
eliminar(id){
     var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
    this.axios.post('/cambiarVisibilidadResultado',{idExamen:id},{headers})
    .then(res=>{
               this.dataTablaResultado =this.dataTablaResultado.filter(e=>{  if(e.idExamen!=id ) return e })
        
         })
    .catch(err=> console.log(err))
}
    },
}
</script>
<style scoped>
    ::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-thumb {
  background: #B71C1C; 
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #EF5350; 
}
</style>