<template>
    <v-container>
        <v-row >
            <v-col sm="10" md="10" lg="5"  class="mx-auto">
                <v-card  color="red darken-4" class="text-center"  dark  >
                       <v-card-title class="justify-center "  >
                        <h3 >Trazabilidad de Exmenes</h3>
                        </v-card-title>       
                        <v-tabs                     
                            color="white"
                            centered
                            v-model="TabsExam" >   
                                <v-tab class="font-weight-bold" 
                                v-for="(item,index) in EstadoExamen"
                                :key="index" :href="'#tab-'+ item.estado"
                                > {{item.estado.toUpperCase()}} 
                                </v-tab>
                        </v-tabs> 
                       <v-tabs-items  v-model="TabsExam" >
                            <v-tab-item      
                             v-for="(item,index) in EstadoExamen" 
                             :key="index" 
                             :value="'tab-'+item.estado"
                            >
                            <v-list class="red lighten-4" max-height="70vh" style="overflow-y: scroll;">                                                    
                               <v-list-item-group>
                                      <v-list-item  class="d-flex my-2 blue-grey lighten-5 px-0 mx-2"  
                                      v-for="element in Clasificador(item.estado)" :key="element.nro" >
                                        <div class="col-11 " :class="{'red lighten-1':(element.estado==='atrasado' && element.matchResultado>0),'blue lighten-1':(element.estado==='pendiente'&& element.matchResultado>0),'green lighten-1':(element.estado==='hoy'&& element.matchResultado>0)}" >
                                            <div  class="d-flex justify-space-between  font-weight-bold">
                                                    <span  :class="element.estado==='atrasado' && element.matchResultado>0?'':'red--text text-darken-4' " class=" " ><v-icon class="mx-1" color="red accent-4">business</v-icon>{{element.examen}}</span>
                                                    <span class="">{{convertidorFecha(element.fechaEntregaResultado)}}</span>
                                            </div>
                                            <div class="d-flex font-weight-bold"> {{element.nombres}} {{element.apellidos}}</div>
                                        </div>
                                            <v-card class="col-1" :class="{'red accent-4':(element.estado==='atrasado'),'blue darken-4':(element.estado==='pendiente'),'green accent-4':(element.estado==='hoy')}"
                                            elevation="5"  rounded width="58" height="70"                                  
                                            ></v-card>
                                        </v-list-item>                                        
                                    </v-list-item-group>
                            </v-list>
                            </v-tab-item>
                        </v-tabs-items>
                </v-card>              
            </v-col>
         <v-col sm="10" md="8" lg="7"  class="mx-auto">   
              <Listahoy ref="tabla"></Listahoy>
        </v-col>
        </v-row>
        
    </v-container>
</template>
<script>
import Listahoy from '../components/ListaExamenesHoy'
import {mapState} from 'vuex'
export default {
    nama:'Trazabilidad',
    components:{Listahoy},
            data() {
                return {
                    url:'',
                    offsetTop: 0,
                    TabsExam:null,
                    archivo:null,
                    descripcion:'',
                    dataListExamenes:[
                    ],
                    EstadoExamen:[{estado:'atrasado'},{estado:'hoy'},{estado:'pendiente'}],
                }
            },
            mounted() {
                this.Clasificador();
                this.listarExamenes();
            },
            computed:{
                        ...mapState(['token'])
            },
            methods: { 
               
               listarExamenes(){
                   this.dataListExamenes=[]
                     var headers =  {
                        "Content-Type": "application/json",
                        token: this.token,
                    };
                       
                   this.axios.get('/listaCompletaDePendientesAdmision',{headers})
                   .then(res=>{
                             console.log(res.data)
                                this.dataListExamenes.push(...res.data);
                                this.dataListExamenes.map(e=> {   var cantidad =this.restarFecha(this.convertidorFecha(e.fechaEntregaResultado));
                                         e.estado= cantidad >0 ?'pendiente':(cantidad==0?'hoy':'atrasado')
                                         return e;      })
                               ;
                   })
                   
                   //this.listarLaboratorio(headers);                   
                                       
                  },
                   listarLaboratorio(headers){
                     this.axios.get('/listarAdmisionExamLaboratorio',{headers})
                    .then(res => {
                        console.log('JEJE yo')
                        console.log(res.data)
                         console.log('JEJE yo')
                            this.dataListExamenes.push(...res.data);
                            this.dataListExamenes.map(e=> {   var cantidad =this.restarFecha(this.convertidorFecha(e.fechaEntregaResultado));
                                         e.estado= cantidad >0 ?'Pendiente':(cantidad==0?'Hoy':'Atrasado')
                                         return e;      })
                            this.listarPatologia(headers);
                            
                    })
                    .catch(err => console.log(err))
                },
                listarPatologia(headers){
                    this.axios.get('/listarAdmisionExamPatologia',{headers})
                    .then(res => {
                           console.log(res.data)
                                this.dataListExamenes.push(...res.data);
                                this.dataListExamenes.map(e=> {   var cantidad =this.restarFecha(this.convertidorFecha(e.fechaEntregaResultado));
                                         e.estado= cantidad >0 ?'pendiente':(cantidad==0?'hoy':'atrasado')
                                         return e;      })
                     })
                    .catch(err => console.log(err))
                     this.$refs.tabla.listarResultados(this.dataListExamenes,headers)
                }, 
                 convertidorFecha(str){
                        if(str==null){
                            return 'Sin Data'
                        }else{
                            const f = new Date(str);
                            var ao=f.getFullYear()
                            var mes=(f.getMonth() + 1)
                                mes=(mes<10)?('0'+mes):mes;
                            var dia=f.getDate()
                                dia=(dia<10)?('0'+dia):dia;
                        return ao+'/'+mes+'/'+dia;
                        }
                },
                restarFecha(e){
                    let fecha1 = new Date(e);
                        let fecha2 = new Date()

                        let resta = fecha1.getTime() - fecha2.getTime()
                        return Math.round(resta/ (1000*60*60*24)) +1;
                    },
                 Clasificador (data){
                 var Lista=[];
                 Lista=this.dataListExamenes.filter(ele=>{ if(ele.estado===data){ return ele.estado ; }})
                  return Lista
                },

  

               
            }         
}
</script>
<style scope>
.v-slide-group__wrapper{
     background-color:#B71C1C !important;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-thumb {
  background: #B71C1C; 
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #EF5350; 
}
#webviewer {
  height: 800px;
}
</style>